.standings {
  padding: 15px 20px;
}
.standings__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.standings__head-title {
  font-weight: 700;
  font-size: 16px;
  color: var(--primary-font);
}
.standings__head button {
  width: 148px;
}
.standings__main hr {
  width: 100%;
  height: 1px;
  background: var(--primary);
  border: none;
  border-radius: 10px;
}
.standings__caption {
  display: grid;
  grid-template-columns: 26px minmax(100px, 1fr) repeat(2, 26px) 120px 38px;
  grid-column-gap: 15px;
  margin-bottom: 10px;
}
.standings__caption > div {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}
.standings__caption > div:nth-child(2) span {
  width: auto;
  padding: 0 10px;
}
.standings__caption span {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  padding: 0 5px;
  font-weight: 500;
  font-size: 12px;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.standings__caption span:hover {
  background: var(--primary);
}
.standings__body-item {
  display: grid;
  grid-template-columns: 26px minmax(100px, 1fr) repeat(2, 26px) 120px 38px;
  grid-column-gap: 15px;
  align-self: center;
  margin: 6px 0;
}
.standings__body-item > div {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}
.standings__body-item > div:nth-child(2) span {
  width: auto;
  padding: 4px 10px 4px 4px;
  cursor: pointer;
}
.standings__body-item > div:nth-child(2) span:hover {
  background: var(--primary);
}
.standings__body-item > div:nth-child(5) span {
  padding: 0;
  justify-content: space-between;
}
.standings__body-item span {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  font-weight: 500;
  font-size: 12px;
  border-radius: 8px;
  transition: all 0.2s ease-in-out;
}
.standings__body-item span .standings__team-img {
  width: 24px;
  height: 24px;
  border-radius: 5px;
  background-color: var(--selected);
  background-size: cover;
  margin-right: 8px;
}
.standings__body-item span .standings__last-five {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  width: 20px;
  background: #00CC00;
  color: #fff;
  border-radius: 5px;
}